import React, { Component } from 'react';
import { Table } from 'antd';
import { moneyToString } from 'utils/formatUtil';

export default class TableStudentData extends Component {
  state = {};

  columns = [
    {
      title: 'Mã SV',
      dataIndex: 'masinhvien',
      key: 'masinhvien',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'hovaten',
      key: 'hovaten',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'ngaysinh',
      key: 'ngaysinh',
    },
    {
      title: 'Giới tính',
      dataIndex: 'gioitinh',
      key: 'gioitinh',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'tenlop',
      key: 'tenlop',
    },
    {
      title: 'Số tiền phải nộp',
      dataIndex: 'sotienphainop',
      key: 'sotienphainop',
      render: text => moneyToString(text),
    },
    {
      title: '% MG',
      dataIndex: 'percentmg',
      key: 'percentmg',
    },
    {
      title: 'Số tiền miễn giảm',
      dataIndex: 'sotienmiengiam',
      key: 'sotienmiengiam',
      render: text => moneyToString(text),
    },
    {
      title: 'Số tiền còn nộp',
      dataIndex: 'sotienconnop',
      key: 'sotienconnop',
      render: text => moneyToString(text),
    },
    {
      title: 'Số tiền đã nộp',
      dataIndex: 'sotiendanop',
      key: 'sotiendanop',
      render: text => moneyToString(text),
    },
    {
      title: 'Ghi chú',
      dataIndex: 'ghichu',
      key: 'ghichu',
    },
    {
      title: 'Ngày cập nhật',
      dataIndex: 'ngaycapnhat',
      key: 'ngaycapnhat',
    },
  ];

  render() {
    const { rowSelection, dataSource } = this.props;
    return (
      <Table
        dataSource={dataSource}
        rowSelection={rowSelection}
        columns={this.columns}
        rowKey="key"
      />
    );
  }
}



